@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
    font-family: 'Roboto', Arial, sans-serif;
    background: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
    min-height: 100vh;
    margin: 0;
}

.fancy-container {
    background: #fff;
    max-width: 400px;
    margin: 60px auto;
    padding: 2.5rem 2rem 2rem 2rem;
    border-radius: 18px;
    box-shadow: 0 8px 32px rgba(60, 60, 60, 0.18);
    text-align: center;
}

.fancy-container h1,
.fancy-container h2 {
    font-weight: 700;
    margin-bottom: 1.5rem;
    color: #f76b1c;
}

.fancy-form label {
    display: block;
    text-align: left;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: #333;
}

.fancy-form input[type="email"],
.fancy-form input[type="password"],
.fancy-form input[type="text"],
.fancy-form input[type="file"] {
    width: 100%;
    padding: 0.7rem;
    margin-bottom: 1.2rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    background: #fafafa;
    transition: border 0.2s;
}

.fancy-form input:focus {
    border: 1.5px solid #f76b1c;
    outline: none;
}

.fancy-form button {
    background: linear-gradient(90deg, #f76b1c 0%, #fad961 100%);
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 0.7rem 2.2rem;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    margin-top: 0.5rem;
    box-shadow: 0 2px 8px rgba(247, 107, 28, 0.08);
    transition: background 0.2s;
}

.fancy-form button:hover {
    background: linear-gradient(90deg, #fad961 0%, #f76b1c 100%);
}

.fancy-message {
    margin-top: 1rem;
    font-size: 1rem;
}

.fancy-message.error {
    color: #e74c3c;
}

.fancy-message.success {
    color: #27ae60;
}
